<template>
	<view>
		<view class="wrap">
			<view v-for="(item,index) in array" :key="index" class="section row" @click="click(index)">
				<view class="title">{{item}}</view>
				<image src="../../../static/ads-seld-right.png"></image>
			</view>
			<view class="section row">
				<view class="title">版本号</view>
				<view class="des">{{version}}</view>
			</view>
		</view>
		<view class="quit" @click="logout">退出登录</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				version: '',
				cacheSize: 0,
				array: []
			}
		},
		onLoad() {
			this.version = getApp().globalData.version
			this.getcache()
			this.array = ['修改密码', '用户协议', '隐私协议', '充值协议']
		},
		methods: {
			click(index) {
				if (index == 0) {
					uni.navigateTo({
						url: './ChangePasswordView',
					});
				} else {
					var url = ''
					if(index == 1){
						url = getApp().globalData.weburl + 'appapi/page/detail?id=' + 4
					}else if(index == 2){
						url = getApp().globalData.weburl + 'appapi/page/detail?id=' + 5
					}else{
						url = getApp().globalData.weburl + 'appapi/page/detail?id=' + 26
					}
					
					console.log(url)
					uni.setStorage({
						key: 'weburl',
						data: url,
						success() {
							uni.navigateTo({
								url: '../../webView/webView',
							});
						}
					})
				}


			},
			getcache() {},
			clearCache() {
				if (this.cacheSize == 0) {
					return
				}
				uni.showModal({
					title: '',
					content: '确定清理缓存吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确认',
					confirmColor: '#7900B2',
					cancelColor: '#000',
					success: res => {
						if (res.confirm) {
							var that = this
							uni.showLoading({
								title: '清理中...',
								mask: false
							});
							setTimeout(() => {
								uni.hideLoading()
								that.cacheSize = 0
								uni.clearStorage()
								uni.showToast({
									title: '已清理缓存',
									icon: 'none'
								});
							}, 1000)
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			logout() {
				uni.showModal({
					title: '确定退出登录吗?',
					content: '',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确认',
					// confirmColor: '#7900B2',
					// cancelColor: '#206CFE',
					success: res => {
						if (res.confirm) {
							uni.clearStorageSync();
							getApp().globalData.userinfo = ''
							uni.reLaunch({
								url: '../../index/index'
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
</script>

<style>
	@import url("./index.css");
</style>
